.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 50px 0;
}

.card {
  margin: 0 2% !important;
}

.infected {
  border-bottom: 10px solid rgba(84, 8, 128, 0.596);
}

.recovered {
  border-bottom: 10px solid rgb(4, 110, 4);
}

.deaths {
  border-bottom: 10px solid rgba(240, 12, 12, 0.856);
}

@media only screen and (max-width: 770px) {
  .card {
    margin: 2% 0 !important;
  }
}
